html, body {
  display: flex;
  flex:1;
  margin: 0;
  padding: 0;
  height: 100%;
}

html {
  background: linear-gradient(180deg, hsl(234, 17%, 12%) 0%, hsl(273, 23%, 18%) 100%);
}

body {
  flex: 1;
  flex-direction: column;
  justify-content: space-evenly;
  background-image: url("../img/bg-stars.svg");
}

div {
  display: flex;
}

.title {
  flex: 1;
  align-items: center;
  justify-content: center;
}

.countdown {
  flex: 1;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.day, .hour, .minute, .second {
  flex: 1;
  max-width: 200px;
  height: 201px;
  margin: 25px;
  background-color: hsl(234, 17%, 12%);
  flex-direction: column;
  border-radius: 20px;
  box-shadow: 0 10px 0 black;
}

.upper {
  flex: 1;
  max-height: 100px;
  border-radius: 20px 20px 0 0;
  align-items: center;
  text-align: center;
  background-color: darken(hsl(236, 21%, 26%), 5%);
  box-shadow: 0 1px 0 black;
  margin-bottom: 1px;
  transition: transform 2s;
  transform-style: preserve-3d;
  transform-origin: bottom;
  backface-visibility: hidden;
  z-index: 1;

  & .number--container {
    overflow: hidden;
    flex: 1;
    align-items: flex-start;
    max-height: 100%;
  }

  & .number {
    color: darken(hsl(345, 95%, 68%), 5%);
    margin: 16% 0 0 0;
  }

  &.back {
    z-index: 0;
    bottom: 50%;
  }
}

.lower-container {
  flex: 1;
  flex-direction: column;
  position: relative;
  bottom: 50%;
}

.upper-container {
  flex: 1;
  flex-direction: column;
}

.lower {
  flex: 1;
  flex-direction: row;
  max-height: 100px;
  border-radius: 0 0 20px 20px;
  align-items: center;
  text-align: center;
  background-color: hsl(236, 21%, 26%);
  transition: transform 2s;
  transform-style: preserve-3d;
  transform-origin: top;
  backface-visibility: hidden;

  & .number--container {
    align-items: flex-end;
    overflow: hidden;
    flex: 1;
    max-height: 100%;
  }

  & .number {
    color: hsl(345, 95%, 68%);
    margin: 0 0 16% 0;
  }

  &.back {
    bottom: 50%;
    transform: rotateX(180deg);
  }
}

.back {
  position: relative;
  width: 200px;
  height: 401px;
  transition: transform 2s;
  transform-style: preserve-3d;
}

.number {
  font-size: 120px;
  flex: 1;
}

.dot {
  position: relative;
  content: "";
  height: 20px;
  min-height: 20px;
  width: 20px;
  border-radius: 10px;
  bottom: calc(150% + 10px);
  z-index: 10;
}

.dot--left {
  right: 10px;
  background: linear-gradient(90deg, transparent 0%, transparent 50%, black 50%, black 100%);;
}

.dot--right {
  left: calc(100% - 10px);
  bottom: calc(150% + 30px);
  background: linear-gradient(90deg, black 0%, black 50%, transparent 50%, transparent 100%);;
}

.link {
  flex: 1;
  background-image: url("../img/pattern-hills.svg");
}

p {
  color: white;
  font-family: Red Hat, sans-serif ;
  letter-spacing: 8px;
  font-weight: 700;
  font-size: 24px;
}
